/* ========================================
   BASE STYLES
   ======================================== */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  color: var(--text-primary);
  background: radial-gradient(
    ellipse at top,
    #0f0f14 0%,
    #0a0a0d 50%,
    #000000 100%
  );
  font-family: var(--font-sans);
  font-weight: var(--font-normal);
  line-height: var(--leading-normal);
  overflow: hidden;
  min-height: 100vh;
}

/* ========================================
   SCROLLBAR (Global)
   ======================================== */

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: var(--radius-md);
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 212, 255, 0.3);
  border-radius: var(--radius-md);
  -webkit-transition: background var(--duration-normal);
  transition: background var(--duration-normal);
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 212, 255, 0.5);
}

/* ========================================
   SELECTION
   ======================================== */

::-moz-selection {
  background: rgba(0, 212, 255, 0.3);
  color: white;
}

::selection {
  background: rgba(0, 212, 255, 0.3);
  color: white;
}

/* ========================================
   FOCUS VISIBLE
   ======================================== */

:focus-visible {
  outline: 2px solid var(--accent-blue);
  outline-offset: 2px;
}

/* ========================================
   LEGACY COMPATIBILITY
   (for widgets using old class names)
   ======================================== */

/* Map old class names to new design system */
.neon-border-blue {
  border: 1px solid var(--accent-blue);
  box-shadow: var(--glow-blue-sm), var(--glow-blue-inset);
}

.shadow-glow-blue {
  box-shadow: var(--glow-blue-md);
}

.shadow-glow-purple {
  box-shadow: var(--glow-purple-md);
}

.shadow-glow-green {
  box-shadow: var(--glow-green-md);
}

.shadow-glow-red {
  box-shadow: var(--glow-red-md);
}

.ease-smooth {
  transition-timing-function: var(--ease-smooth);
}

/* ========================================
   UTILITY CLASSES
   ======================================== */

